<template>
  <section id="profile" class="container-fluid">
    <h1 class="display-1">Profil</h1>
    <img referrerpolicy="no-referrer" :src="profile.photoURL" alt="Fotografija korisnika">
    <p>{{ profile.displayName }}</p>
    <p class="lead">{{ profile.email }}</p>
  </section>
  <section id="moji-apartmani" class="container-fluid">
    <h2>Moji Apartmani:</h2>
    <transition name="fade" mode="in-out">
      <div v-if="isClicked" class="alert alert-success" role="alert">
          Podatci su uspješno ažurirani!
      </div>
    </transition>
    <transition name="fade" mode="in-out">
      <div v-if="isClickedDel" class="alert alert-success" role="alert">
          Apartman uspješno izbrisan!
      </div>
    </transition>
    <ul 
      v-for="apartman in mojiApartmani"
      :key="apartman.id"
      :id="apartman.id"
    >
      <li>
        <div>
          <h3>{{ apartman.nazivObjekta }}</h3>
          <p>{{ apartman.grad }}, {{ apartman.ulica }}</p>
        </div>
        <div class="upravljanje">
          <i @click="editApartman(apartman)" class="fas fa-pencil-alt fa-2x"></i>
          <i @click="deleteApartman(apartman.id)" class="fas fa-trash text-danger fa-2x"></i>
        </div>
      </li>
    </ul>
  </section>
  <div class="modal fade" id="edit" tabindex="-1" aria-labelledby="editLabel" aria-hidden="true">
    <div class="modal-dialog">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title" id="editLabel">Uređivanje podataka:</h5>
          <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
        </div>
        <div class="modal-body">
          <div class="form-control">
            <label for="nazivObjekta">Naziv Objekta:</label>
            <input type="text" id="nazivObjekta" v-model.trim="apartman.nazivObjekta">
          </div>
          <div class="form-control">
            <label for="tipObjekta">Tip objekta:</label>
            <select name="tipObjekta" id="tipObjekta" v-model.trim="apartman.tipObjekta">
              <option value="apartman">Apartman</option>
              <option value="kuca">Kuća</option>
              <option value="soba">Soba</option>
            </select>
          </div>
          <div class="form-control">
            <label for="ulica">Ulica:</label>
            <input type="text" id="ulica" v-model.trim="apartman.ulica">
          </div>
          <div class="form-control">
            <label for="grad">Grad:</label>
            <input type="text" id="grad" v-model.trim="apartman.grad">
          </div>
          <div class="form-control">
            <label for="zupanija">Županija:</label>
            <select name="zupanija" id="zupanija" class="form-select" v-model="apartman.zupanija" aria-label="Default select example">
              <option selected>Open this select menu</option>
              <option value="Zagrebačka županija">Zagrebačka županija</option>
              <option value="Krapinsko-zagorska županija">Krapinsko-zagorska županija</option>
              <option value="Sisačko-moslavačka županija">Sisačko-moslavačka županija</option>
              <option value="Karlovačka županija">Karlovačka županija</option>
              <option value="Varaždinska županija">Varaždinska županija</option>
              <option value="Koprivničko-križevačka županija">Koprivničko-križevačka županija</option>
              <option value="Bjelovarsko-bilogorska županija">Bjelovarsko-bilogorska županija</option>
              <option value="Primorsko-goranska županija">Primorsko-goranska županija</option>
              <option value="Ličko-senjska županija">Ličko-senjska županija</option>
              <option value="Virovitičko-podravska županija">Virovitičko-podravska županija</option>
              <option value="Požeško-slavonska županija">Požeško-slavonska županija</option>
              <option value="Brodsko-posavska županija">Brodsko-posavska županija</option>
              <option value="Zadarska županija">Zadarska županija</option>
              <option value="Osječko-baranjska županija">Osječko-baranjska županija</option>
              <option value="Šibensko-kninska županija">Šibensko-kninska županija</option>
              <option value="Vukovarsko-srijemska županija">Vukovarsko-srijemska županija</option>
              <option value="Splitsko-dalmatinska županija">Splitsko-dalmatinska županija</option>
              <option value="Istarska županija">Istarska županija</option>
              <option value="Dubrovačko-neretvanska županija">Dubrovačko-neretvanska županija</option>
              <option value="Međimurska županija">Međimurska županija</option>
              <option value="Grad Zagreb">Grad Zagreb</option>
            </select>
          </div>
          <div class="form-control">
            <label for="postanskiBroj">Postanski broj:</label>
            <input type="number" id="postanskiBroj" v-model.number="apartman.postanskiBroj">
          </div>
          <div class="form-control">
            <label for="povrsinaApartmana">Povrsina apartmana u m2:</label>
            <input type="number" id="povrsinaApartmana" v-model.number="apartman.povrsinaApartmana">
          </div>
          <div class="form-control">
            <label for="brojOsoba">Maksimalni broj osoba:</label>
            <input type="number" id="brojOsoba" v-model.number="apartman.brojOsoba">
          </div>
          <div class="form-control">
            <label for="brojLezaja">Broj lezaja:</label>
            <input type="text" id="brojLezaja" v-model.number="apartman.brojLezaja">
          </div>
          <div class="form-control">
            <label for="cijenaNocenja">Cijena noćenja:</label>
            <input type="number" id="cijenaNocenja" v-model.number="apartman.cijenaNocenja">
          </div>
          <div class="form-control">
            <label for="opis">Opis:</label>
            <textarea row="4" id="opis" v-model.trim="apartman.opis"></textarea>
          </div>
          <div class="form-control">
            <label for="link">Promjeni poveznicu:</label>
            <input type="text" id="link" v-model.number="apartman.poveznica">
          </div>
          <div class="form-control">
            <label for="opis">Slike:</label>
            <div class="img-controls">
              <input
                type="file"
                @change="onFilePicked"
                ref="fileInput"
                accept="image/*"
              >
              <div class="img-wrapp" v-if="image">
                <img :src="apartman.imageUrl" alt="Slika">
                <i class="fas fa-times" @click="deleteImage(apartman.id)"></i>
              </div>
            </div>
          </div>
        </div>
        <div class="modal-footer">
          <button type="button" class="btn btn-secondary" data-bs-dismiss="modal">Close</button>
          <button @click="updateApartman" type="button" class="btn btn-primary">Save changes</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import firebase from '../firebase'
import {Modal} from 'bootstrap'

export default {
  name: 'Profil',
  props: ['id'],
  data() {
    return {
      profile: '',
      mojiApartmani: [],
      activeApartman: null,
      isClicked: false,
      isClickedDel: false,
      modal: '',
      apartman: {
        nazivObjekta: null,
        tipObjekta: null,
        ulica: null,
        grad: null,
        zupanija: null,
        postanskiBroj: null,
        povrsinaApartmana: null,
        brojOsoba: null,
        brojLezaja: null,
        cijenaNocenja: null,
        opis: null,
        poveznica: null,
        imageUrl: null
      },
      image: null
    }
  },
  methods: {
    onPickFile() {
      this.$refs.fileInput.click()
    },
    onFilePicked(event) {
      const files = event.target.files
      let filename = files[0].name;
      if(filename.lastIndexOf('.') <= 0) {
        return alert('Please add a valid file!')
      }
      const fileReader = new FileReader()
      fileReader.addEventListener('load', () => {
        this.imageURL = fileReader.result
      })
      fileReader.readAsDataURL(files[0])
      this.image = files[0]
      console.log(this.image);
    },
    deleteImage(id) {
      let image = firebase.storage().ref('apartmani').child(id + '.jpg')
      const that = this
      image.delete().then(function() {
        console.log('image deleted');
        that.apartman.imageUrl = ''
      }).catch(function(error) {
        console.log(error);
      })
    },
    editApartman(apartman) {
      this.modal = new Modal(document.getElementById('edit'))
      this.modal.show()
      this.apartman = apartman
      console.log(apartman.id);
      console.log(this.apartman);
      this.activeApartman = apartman.id
    },
    updateApartman() {
      const state = this
      let imageUrl
      let apartmanRef = this.apartman.id
      if(this.image) {
        firebase.firestore().collection('apartmani').doc(apartmanRef).update({
        }).then(() => {
          const filename = state.image.name
          const ext = filename.slice(filename.lastIndexOf('.'))
          let uploadTask = firebase.storage().ref('apartmani/' + apartmanRef + ext).put(state.image)
          uploadTask.on('state_changed', () => {
  
          }, () => {
            // Handle unseccessful uploads
          }, () => {
            // Handle successful uploads on complete
            uploadTask.snapshot.ref.getDownloadURL().then((downloadURL) => {
              imageUrl = downloadURL
              firebase.firestore().collection('apartmani').doc(apartmanRef).update({imageUrl: imageUrl})
            })
          })
        })
      }

      firebase.firestore().collection('apartmani').doc(this.activeApartman).update(this.apartman)
        .then(function() {
          state.isClicked = true;
          setTimeout(() => {
            state.isClicked = false
          }, 2000)
          state.modal.hide()
        });
    },
    deleteApartman(apartmanId) {
      const state = this
      firebase.firestore().collection("apartmani").doc(apartmanId).delete()
        .then(() => {
          state.isClickedDel = true;
          setTimeout(() => {
            state.isClickedDel = false
          }, 2000)
        })
      .catch((error) => {
          console.error("Error removing document: ", error);
      });
    }
  },
  created() {
    firebase.auth().onAuthStateChanged((user) => {
      if (user) {
        this.profile = user
        this.mojiApartmani = this.$store.getters['apartmani/apartmani'].find((apartman) => apartman.userId === user.uid);
        firebase.firestore().collection("apartmani").where("userId.id", "==", user.uid)
          .onSnapshot((querySnapshot) => {
            this.mojiApartmani = [];
              querySnapshot.forEach((doc) => {
                  const apartman = {
                      id: doc.id,
                    ...doc.data()
                  }
                  this.mojiApartmani.push(apartman)
              });
          })
      } else {
        this.profile = '',
        this.mojiApartmani = [],
        this.$router.replace('/apartmani');
        this.isLoggedIn = false
      }
    });
  }
}
</script>

<style scoped>
.img-wrapp {
  position: relative;
}
.img-wrapp i {
  position: absolute;
  top: 5px;
  left: 130px;
  color: red;
}
.img-wrapp i:hover {
  cursor: pointer;
}
.alert {
  width: 60%;
  text-align: center;
}
.fade-enter-active, .fade-leave-active {
  transition: opacity 1.3s ease;
}
.fade-enter, .fade-leave-to {
  opacity: 0;
}
#profile {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 50px;
}
#profile img {
  border-radius: 20px;
  width: 150px;
  margin-bottom: 30px;
}
#profile p {
  font-size: 1.5rem;
}
#moji-apartmani {
  margin-top: 100px;
  display: flex;
  flex-direction: column;
  align-items: center;
}
#moji-apartmani h2 {
  margin-bottom: 30px;
}
#moji-apartmani ul {
  list-style-type: none;
  padding: 0;
}
#moji-apartmani i {
  cursor: pointer;
}
#moji-apartmani ul li {
  width: 600px;
  height: 200px;
  margin-bottom: 30px;
  border-radius: 20px;
  box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 80px;
}
#moji-apartmani ul li .upravljanje i {
  margin: 0 15px;
}
.form-control {
  border: none;
  display: flex;
  align-items: center;
}
.form-control label {
  margin-right: 10px;
  width: 100%;
}
.form-control input, .form-control select {
  height: 40px;
  width: 100%;
  border-radius: 10px;
  padding: 0 20px;
}
.form-control textarea {
  height: 100px;
  width: 200%;
  border-radius: 10px;
  padding: 10px 20px;
}
.form-control img {
  height: 100px;
  width: 100%;
}
.img-controls {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 200%;
}
@media only screen and (max-width: 966px) {
  .alert {
    width: 90%;
  }
  #moji-apartmani ul li {
    width: 310px;
    height: 200px;
    margin-bottom: 30px;
    padding: 0 20px;
  }
  #moji-apartmani ul li .upravljanje {
    display: flex;
    flex-direction: column;
  }
  #moji-apartmani ul li .upravljanje i {
    margin: 10px 0;
  }
}
</style>