import firebase from '../../firebase.js'

export default {
  namespaced: true,
  state() {
    return {
      apartmani: []
    }
  },
  actions: {
    noviApartman(context, data) {
      const apartmanId = firebase.firestore().collection('apartmani').doc();
      const podatciApartmana = {
          userId: data.userId,
          nazivObjekta: data.nazivObjekta,
          tipObjekta: data.tipObjekta,
          ulica: data.ulica,
          grad: data.grad,
          zupanija: data.zupanija,
          postanskiBroj: data.postanskiBroj,
          povrsinaApartmana: data.povrsinaApartmana,
          brojOsoba: data.brojOsoba,
          brojLezaja: data.brojLezaja,
          cijenaNocenja: data.cijenaNocenja,
          opis: data.opis,
          poveznica: data.poveznica
      }

      let imageUrl
      let apartmanRef = apartmanId.id

      apartmanId.set({
        ...podatciApartmana
      }).then(() => {
        const filename = data.image.name
        const ext = filename.slice(filename.lastIndexOf('.'))
        let uploadTask = firebase.storage().ref('apartmani/' + apartmanRef + ext).put(data.image)
        uploadTask.on('state_changed', () => {

        }, () => {
          // Handle unseccessful uploads
        }, () => {
          // Handle successful uploads on complete
          uploadTask.snapshot.ref.getDownloadURL().then((downloadURL) => {
            imageUrl = downloadURL
            firebase.firestore().collection('apartmani').doc(apartmanRef).update({imageUrl: imageUrl})
          })
        })
      }).then(() => {
        context.commit('noviApartman', {
          ...podatciApartmana,
          imageUrl: imageUrl,
          apartmanId: apartmanId
        });
      }).catch((error) => {
        console.log(error);
      })
    },
    async ucitajApartmane(context) {
      await firebase.firestore().collection("apartmani").get().then((querySnapshot) => {
        const apartmani = [];
        querySnapshot.forEach(doc => {
            const apartman = {
              id: doc.id,
              ...doc.data()
            };
            apartmani.push(apartman)

          });
          context.commit('postaviApartmane', apartmani);
        });
    }
  },
  mutations: {
    noviApartman(state, payload) {
      state.apartmani.push(payload);
    },
    postaviApartmane(state, payload) {
      state.apartmani = payload;
    }
  },
  getters: {
    apartmani(state) {
      return state.apartmani;
    },
    // Provjeravamo da li postoje apartmani u listi
    sadrziApartmane(state) {
      return state.apartmani && state.apartmani.length > 0;
    }
  }
}