<template>
  <section id="pretrazivanje" class="container-fluid">
    <h2 class="text-center">Filtriranje apartmana:</h2>
    <div class="pretrazivanje">
      <div class="text-search">
        <input type="text" v-model="search" placeholder="Naziv apartmana.."/>
      </div>
      <div class="dropdown">
        <select class="form-select" v-model="zupanija" aria-label="Default select example">
          <option selected>Odaberite županiju</option>
          <option value="Zagrebačka županija">Zagrebačka županija</option>
          <option value="Krapinsko-zagorska županija">Krapinsko-zagorska županija</option>
          <option value="Sisačko-moslavačka županija">Sisačko-moslavačka županija</option>
          <option value="Karlovačka županija">Karlovačka županija</option>
          <option value="Varaždinska županija">Varaždinska županija</option>
          <option value="Koprivničko-križevačka županija">Koprivničko-križevačka županija</option>
          <option value="Bjelovarsko-bilogorska županija">Bjelovarsko-bilogorska županija</option>
          <option value="Primorsko-goranska županija">Primorsko-goranska županija</option>
          <option value="Ličko-senjska županija">Ličko-senjska županija</option>
          <option value="Virovitičko-podravska županija">Virovitičko-podravska županija</option>
          <option value="Požeško-slavonska županija">Požeško-slavonska županija</option>
          <option value="Brodsko-posavska županija">Brodsko-posavska županija</option>
          <option value="Zadarska županija">Zadarska županija</option>
          <option value="Osječko-baranjska županija">Osječko-baranjska županija</option>
          <option value="Šibensko-kninska županija">Šibensko-kninska županija</option>
          <option value="Vukovarsko-srijemska županija">Vukovarsko-srijemska županija</option>
          <option value="Splitsko-dalmatinska županija">Splitsko-dalmatinska županija</option>
          <option value="Istarska županija">Istarska županija</option>
          <option value="Dubrovačko-neretvanska županija">Dubrovačko-neretvanska županija</option>
          <option value="Međimurska županija">Međimurska županija</option>
          <option value="Grad Zagreb">Grad Zagreb</option>
        </select>
      </div>
      <button type="button" class="btn btn-secondary" @click="resetirajFilter">Resetiraj</button>
    </div>
  </section>
  <section id="prikaz-apartmana" class="container-fluid">
    <h1>Lista svih apartmana</h1>
    <ul v-if="sadrziApartmane">
      <apartmani-kartica
        v-for="apartman in filteredList"
        :key="apartman.id"
        :id="apartman.id"
        :nazivObjekta="apartman.nazivObjekta"
        :grad="apartman.grad"
        :ulica="apartman.ulica"
        :zupanija="apartman.zupanija"
        :user="apartman.userId.name"
      >
      </apartmani-kartica>
    </ul>
    <h3 v-else>Nema pronađenih apartmana.</h3>
  </section>
</template>

<script>
import ApartmaniKartica from '../components/apartmani/ApartmanKartica.vue'

export default {
  components: {
    ApartmaniKartica
  },
  data() {
    return {
      search: '',
      zupanija: '',
      listaApartmana: []
    }
  },
  computed: {
    filtriraniApartmani() {
      return this.$store.getters['apartmani/apartmani']
    },
    sadrziApartmane() {
      return this.$store.getters['apartmani/sadrziApartmane']
    },
    filteredList() {
      return this.listaApartmana.filter(apartman => {
        if (this.zupanija === '') {
          return apartman.nazivObjekta.toLowerCase().includes(this.search.toLowerCase())
        } else if(this.search === '') {
          return !apartman.zupanija.indexOf(this.zupanija)
        } else {
          return apartman.nazivObjekta.toLowerCase().includes(this.search.toLowerCase()) && !apartman.zupanija.indexOf(this.zupanija)
        }
      })
    }
  },
  methods: {
    resetirajFilter() {
      this.search = ''
      this.zupanija = ''
    },
    ucitajApartmane() {
      this.$store.dispatch('apartmani/ucitajApartmane');
    }
  },
  updated() {
    this.listaApartmana = this.filtriraniApartmani
  },
  created() {
    this.ucitajApartmane();
  }
}
</script>

