<template>
  <section id="forma">
    <form @submit.prevent="submit">
      <div class="form-control">
        <label for="nazivObjekta">Naziv Objekta:</label>
        <input type="text" id="nazivObjekta" v-model.trim="nazivObjekta">
      </div>
      <div class="form-control">
        <label for="tipObjekta">Tip objekta:</label>
        <select name="tipObjekta" id="tipObjekta" v-model.trim="tipObjekta">
          <option value="Apartman">Apartman</option>
          <option value="Kuća">Kuća</option>
          <option value="Soba">Soba</option>
        </select>
      </div>
      <div class="form-control">
        <label for="ulica">Ulica:</label>
        <input type="text" id="ulica" v-model.trim="ulica">
      </div>
      <div class="form-control">
        <label for="grad">Grad:</label>
        <input type="text" id="grad" v-model.trim="grad">
      </div>
      <div class="form-control">
        <label for="zupanija">Županija:</label>
        <select name="zupanija" class="form-select" v-model="zupanija" aria-label="Default select example">
          <option selected>Open this select menu</option>
          <option value="Zagrebačka županija">Zagrebačka županija</option>
          <option value="Krapinsko-zagorska županija">Krapinsko-zagorska županija</option>
          <option value="Sisačko-moslavačka županija">Sisačko-moslavačka županija</option>
          <option value="Karlovačka županija">Karlovačka županija</option>
          <option value="Varaždinska županija">Varaždinska županija</option>
          <option value="Koprivničko-križevačka županija">Koprivničko-križevačka županija</option>
          <option value="Bjelovarsko-bilogorska županija">Bjelovarsko-bilogorska županija</option>
          <option value="Primorsko-goranska županija">Primorsko-goranska županija</option>
          <option value="Ličko-senjska županija">Ličko-senjska županija</option>
          <option value="Virovitičko-podravska županija">Virovitičko-podravska županija</option>
          <option value="Požeško-slavonska županija">Požeško-slavonska županija</option>
          <option value="Brodsko-posavska županija">Brodsko-posavska županija</option>
          <option value="Zadarska županija">Zadarska županija</option>
          <option value="Osječko-baranjska županija">Osječko-baranjska županija</option>
          <option value="Šibensko-kninska županija">Šibensko-kninska županija</option>
          <option value="Vukovarsko-srijemska županija">Vukovarsko-srijemska županija</option>
          <option value="Splitsko-dalmatinska županija">Splitsko-dalmatinska županija</option>
          <option value="Istarska županija">Istarska županija</option>
          <option value="Dubrovačko-neretvanska županija">Dubrovačko-neretvanska županija</option>
          <option value="Međimurska županija">Međimurska županija</option>
          <option value="Grad Zagreb">Grad Zagreb</option>
        </select>
      </div>
      <div class="form-control">
        <label for="postanskiBroj">Postanski broj:</label>
        <input type="number" id="postanskiBroj" v-model.number="postanskiBroj">
      </div>
      <div class="form-control">
        <label for="povrsinaApartmana">Povrsina apartmana u m2:</label>
        <input type="number" id="povrsinaApartmana" v-model.number="povrsinaApartmana">
      </div>
      <div class="form-control">
        <label for="brojOsoba">Maksimalni broj osoba:</label>
        <input type="number" id="brojOsoba" v-model.number="brojOsoba">
      </div>
      <div class="form-control">
        <label for="brojLezaja">Broj lezaja:</label>
        <input type="text" id="brojLezaja" v-model.number="brojLezaja">
      </div>
      <div class="form-control">
        <label for="cijenaNocenja">Cijena noćenja (u eurima):</label>
        <input type="number" id="cijenaNocenja" v-model.number="cijenaNocenja">
      </div>
      <div class="form-control">
        <label for="opis">Opis:</label>
        <textarea row="4" id="opis" v-model.trim="opis"></textarea>
      </div>
      <div class="form-control">
        <label for="link">Ukoliko već imate iznajmljen apartman na nekoj stranici, zaljepite poveznicu ovdje (nije obavezno):</label>
        <input type="text" id="link" v-model.number="poveznica">
      </div>
      <div class="form-control">
        <label for="opis">Slike:</label>
        <input
          type="file"
          @change="onFilePicked"
          ref="fileInput"
          accept="image/*"
        >
          <img :src="imageURL" alt="">
      </div>
      <button class="btn btn-primary">Objavi</button>
    </form>
  </section>
</template>

<script>
export default {
  emits: ['save-data'],
  data() {
    return {
      nazivObjekta: '',
      tipObjekta: '',
      ulica: '',
      grad: '',
      zupanija: '',
      postanskiBroj: null,
      povrsinaApartmana: null,
      brojOsoba: null,
      brojLezaja: null,
      cijenaNocenja: null,
      opis: '',
      poveznica: '',
      userId: '',
      imageURL: '',
      image: null
    };
  },
  methods: {
    onPickFile() {
      this.$refs.fileInput.click()
    },
    onFilePicked(event) {
      const files = event.target.files
      let filename = files[0].name;
      if(filename.lastIndexOf('.') <= 0) {
        return alert('Please add a valid file!')
      }
      const fileReader = new FileReader()
      fileReader.addEventListener('load', () => {
        this.imageURL = fileReader.result
      })
      fileReader.readAsDataURL(files[0])
      this.image = files[0]
    },
    // Add form validation
    submit() {
      if (!this.image) {
        return;
      }
      const formData = {
        nazivObjekta: this.nazivObjekta,
        tipObjekta: this.tipObjekta,
        ulica: this.ulica,
        grad: this.grad,
        zupanija: this.zupanija,
        postanskiBroj: this.postanskiBroj,
        povrsinaApartmana: this.povrsinaApartmana,
        brojOsoba: this.brojOsoba,
        brojLezaja: this.brojLezaja,
        cijenaNocenja: this.cijenaNocenja,
        opis: this.opis,
        poveznica: this.poveznica,
        userId: this.$store.state.korisnik.user,
        image: this.image
      };

      this.$emit('save-data', formData);
    }
  }
}
</script>

<style scoped>
.img-container {
  position: relative;
}
.delete-img {
  position: absolute;
  top: 0;
  left: 10px;
}
.form-control {
  margin: 0.5rem 0;
}
label {
  font-weight: bold;
  display: block;
  margin-bottom: 0.5rem;
}
input[type='checkbox'] + label {
  font-weight: normal;
  display: inline;
  margin: 0 0 0 0.5rem;
}
input,
textarea,
select {
  display: block;
  width: 100%;
  border: 1px solid #ccc;
  font: inherit;
}
input:focus,
textarea:focus {
  background-color: #f0e6fd;
  outline: none;
  border-color: #3d008d;
}
input[type='checkbox'] {
  display: inline;
  width: auto;
  border: none;
}
input[type='checkbox']:focus {
  outline: #3d008d solid 1px;
}
h3 {
  margin: 0.5rem 0;
  font-size: 1rem;
}
.invalid label {
  color: red;
}
.invalid input,
.invalid textarea {
  border: 1px solid red;
}
#forma {
  margin-bottom: 30px;
}
#forma form .form-control {
  border: none;
}
#forma form .form-control label {
  margin: 0 0 15px 20px;
}
#forma form .form-control input, #forma form .form-control select {
  height: 40px;
  border-radius: 10px;
  padding: 0 20px;
}
#forma form .form-control textarea {
  height: 100px;
  border-radius: 10px;
  padding: 10px 20px;
}
button {
  display: flex;
  align-items: center;
  margin: 20px auto;
}
img {
  margin-top: 20px;
  width: 100%;
}
@media only screen and (max-width: 966px) {
  img {
    margin-top: 20px;
    width: 100%;
  }
}
</style>