<template>
  <section id="apartmani-detaljno" class="container-fluid text-center">
    <img v-if="odabraniApartman.imageUrl" :src="odabraniApartman.imageUrl" alt="Slika apartmana">
    <h1 class="display-1">{{ odabraniApartman.nazivObjekta }}</h1>
    <h2>{{ odabraniApartman.ulica }}, {{ odabraniApartman.grad }}, {{ odabraniApartman.zupanija }}, {{ odabraniApartman.postanskiBroj }}</h2>
    <p>Cijena najma: {{ odabraniApartman.cijenaNocenja }} €/noć</p>
    <p>Tip objekta: {{ odabraniApartman.tipObjekta }}</p>
    <p>Povrsina apartmana: {{ odabraniApartman.povrsinaApartmana }} m2</p>
    <p>Broj osoba: {{ odabraniApartman.brojOsoba }}</p>
    <p>Broj lezaja: {{ odabraniApartman.brojLezaja }}</p>
    <p>Opis:</p>
    <p>{{ odabraniApartman.opis }}</p>
  </section>
  <section id="profil">
    <h2>Vlasnik apartmana:</h2>
    <img referrerpolicy="no-referrer" :src="odabraniApartman.userId.image" alt="Fotografija korisnika">
    <p>{{ odabraniApartman.userId.name }}</p>
    <p class="lead">{{ odabraniApartman.userId.email }}</p>
    <p v-if="odabraniApartman.poveznica" class="text-center px-2">Apartman možete još pogledati na sljedećem mjestu: <a :href="odabraniApartman.poveznica" target="_new">Pogledaj</a></p>
  </section>
  <div class="text-center my-5">
    <button @click="$router.go(-1)" class="btn btn-primary">
      <i class="fas fa-angle-left"></i>
      Povratak
    </button>
  </div>
</template>

<script>
export default {
  props: ['id'],
  data() {
    return {
      odabraniApartman: {}
    }
  },
  methods: {
    ucitajApartman() {
      this.odabraniApartman = this.$store.getters['apartmani/apartmani'].find((apartman) => apartman.id === this.id);
    }
  },
  created() {
    this.ucitajApartman();
  },
}
</script>

<style scoped>
i {
  margin-right: 10px;
}
a {
  cursor: pointer;
}
.btn {
  width: 150px;
  height: 50px;
}
.button {
    margin-top: 30px;
}
#apartmani-detaljno {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 50px;
}
#apartmani-detaljno img {
  margin-bottom: 30px;
  width: 80%;
}
#apartmani-detaljno h2 {
  margin: 30px 0;
}
#profil {
  margin-top: 50px;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}
#profil h2 {
  margin-bottom: 20px;
}
#profil img {
  border-radius: 20px;
  width: 150px;
  margin-bottom: 30px;
}
@media only screen and (max-width: 966px) {
  img {
    width: 100%;
  }
}
</style>